.schedule{
  padding: 20px;
  .block{
    border: 1px solid #000;
    width: 150px;
    float: left;
    margin-right: 20px;
    padding: 5px;
    margin-bottom: 20px;
    &:nth-child(2n){
      margin-right: 0;
    }
    .row{
      border-bottom: 1px solid #000;
      height: 10px;
    }
  }
}
